import React from "react";
import {
    Layout,
    Form,
    Input,
    Select,
    Switch,
    Row,
    Col,
    Button,
    Descriptions,
    Table
} from "antd";

const { Option } = Select;

class Index extends React.Component {
    state = {
        isCalc: false,
        isProbation: false,
        initSalary: 0,
        attendSalary: 0,
        fiveInsurance: [],
        fiveInsuranceSalary: 0,
        tax: [],
        finalSalary: 0
    };

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                this.calc(values);
            }
        });
    };

    calc(values) {
        const { salary, probation, ycqts, taxDeduct } = values;
        let _salary = Math.max(+salary, 0),
            _probation = !!probation,
            _ycqts = +ycqts,
            _ycqr = _ycqts === -1 ? 1 : Math.min(_ycqts / 21.75, 1),
            _taxDeduct = Math.max(+taxDeduct, 0),
            _fix = this.numFix;
        let isProbation = _probation,
            initSalary = _fix(_probation ? _salary * 0.8 : _salary),
            attendSalary = _fix(initSalary * _ycqr),
            fiveInsurance = [
                {
                    key: 1,
                    name: "养老保险(8%|16%)",
                    person: _fix(initSalary * 0.08),
                    company: _fix(initSalary * 0.16),
                    sum: _fix(initSalary * 0.24)
                },
                {
                    key: 2,
                    name: "医疗保险(2%|9.5%)",
                    person: _fix(initSalary * 0.02),
                    company: _fix(initSalary * 0.095),
                    sum: _fix(initSalary * 0.115)
                },
                {
                    key: 3,
                    name: "失业保险(0.5%|0.5%)",
                    person: _fix(initSalary * 0.005),
                    company: _fix(initSalary * 0.005),
                    sum: _fix(initSalary * 0.01)
                },
                {
                    key: 4,
                    name: "生育保险(0%|1%)",
                    person: 0,
                    company: _fix(initSalary * 0.01),
                    sum: _fix(initSalary * 0.01)
                },
                {
                    key: 5,
                    name: "工伤保险(0%|0.16%)",
                    person: 0,
                    company: _fix(initSalary * 0.0016),
                    sum: _fix(initSalary * 0.0016)
                },
                {
                    key: 6,
                    name: "住房公积金(7%|7%)",
                    person: _fix(initSalary * 0.07),
                    company: _fix(initSalary * 0.07),
                    sum: _fix(initSalary * 0.14)
                },
                {
                    key: 7,
                    name: "合计(17.5%|27.16%)",
                    person: _fix(initSalary * 0.175),
                    company: _fix(initSalary * 0.2716),
                    sum: _fix(initSalary * 0.4466)
                }
            ],
            fiveInsuranceSalary = _fix(
                Math.max(
                    attendSalary -
                        fiveInsurance[fiveInsurance.length - 1].person,
                    0
                )
            ),
            tax = [
                {
                    key: 1,
                    name: "个税起征点",
                    count: _fix(5000 + _taxDeduct)
                },
                {
                    key: 2,
                    name: "应纳税所得额",
                    count: _fix(
                        Math.max(fiveInsuranceSalary - (5000 + _taxDeduct), 0)
                    )
                },
                {
                    key: 3,
                    name: "个税",
                    count: _fix(
                        this.calcTax(fiveInsuranceSalary - (5000 + _taxDeduct))
                    )
                }
            ],
            finalSalary = _fix(fiveInsuranceSalary - tax[tax.length - 1].count);

        this.setState({
            isProbation,
            initSalary,
            attendSalary,
            fiveInsurance,
            fiveInsuranceSalary,
            tax,
            finalSalary,
            isCalc: true
        });
    }

    calcTax(val) {
        let _val = val,
            _tax = 0,
            _taxList = [
                [960000, 0.45],
                [660000, 0.35],
                [420000, 0.3],
                [300000, 0.25],
                [144000, 0.2],
                [36000, 0.1],
                [0, 0.03]
            ];

        _taxList.forEach(v => {
            if (_val > v[0]) {
                _tax += (_val - v[0]) * v[1];
                _val = v[0];
            }
        });

        return _tax;
    }

    numFix(num) {
        return +num.toFixed(2);
    }

    render() {
        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4, offset: 6 }
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 6 }
            }
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0
                },
                sm: {
                    span: 14,
                    offset: 10
                }
            }
        };
        const tableColumns = [
            {
                title: "项目",
                dataIndex: "name",
                key: "name",
                width: "40%"
            },
            {
                title: "个人",
                dataIndex: "person",
                key: "person"
            },
            {
                title: "公司",
                dataIndex: "company",
                key: "company"
            },
            {
                title: "合计",
                dataIndex: "sum",
                key: "sum"
            }
        ];
        const table2Columns = [
            {
                title: "项目",
                dataIndex: "name",
                key: "name",
                width: "40%"
            },
            {
                title: "金额",
                dataIndex: "count",
                key: "count"
            }
        ];
        const {
            isProbation,
            initSalary,
            attendSalary,
            fiveInsurance,
            fiveInsuranceSalary,
            tax,
            finalSalary,
            isCalc
        } = this.state;

        return (
            <Layout style={{ background: "#fff" }}>
                <Layout.Header
                    style={{
                        fontSize: "26px",
                        fontWeight: "bold",
                        boxShadow: "0 0 4px rgba(0,0,0,0.5)",
                        background: "#fff"
                    }}
                >
                    威派格员工薪资计算器2019
                </Layout.Header>
                <Layout.Content style={{ padding: "20px 0" }}>
                    <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label="薪资">
                            {getFieldDecorator("salary", {
                                rules: [
                                    {
                                        required: true,
                                        message: "请输入您的薪资"
                                    }
                                ]
                            })(
                                <Input
                                    type="number"
                                    placeholder="请输入您的薪资"
                                />
                            )}
                        </Form.Item>
                        <Form.Item
                            label="是否试用期"
                            help="试用期薪资为合同薪资的80%"
                        >
                            {getFieldDecorator("probation", {
                                valuePropName: "checked"
                            })(<Switch />)}
                        </Form.Item>
                        <Form.Item
                            label="出勤天数"
                            help="出勤满额为每月21.75天"
                        >
                            {getFieldDecorator("ycqts", {
                                initialValue: "-1"
                            })(
                                <Select>
                                    <Option value="-1">全勤</Option>
                                    {new Array(23).fill(0).map((v, i) => (
                                        <Option value={i} key={i}>
                                            {i}
                                        </Option>
                                    ))}
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item
                            label="个税扣除"
                            help="个税专项附加扣除累计金额"
                        >
                            {getFieldDecorator("taxDeduct", {
                                initialValue: 0
                            })(
                                <Input
                                    type="number"
                                    placeholder="请输入您的个税扣除总额"
                                />
                            )}
                        </Form.Item>
                        <Form.Item {...tailFormItemLayout}>
                            <Button type="primary" htmlType="submit">
                                计算
                            </Button>
                        </Form.Item>
                    </Form>
                </Layout.Content>
                {isCalc && (
                    <Layout.Content style={{ padding: "20px 0" }}>
                        <Row>
                            <Col span={16} offset={4}>
                                <Descriptions title="薪资明细" bordered>
                                    <Descriptions.Item
                                        label={
                                            "初始薪资" +
                                            (isProbation ? "(试用期80%)" : "")
                                        }
                                        span={3}
                                    >
                                        {initSalary}
                                    </Descriptions.Item>
                                    <Descriptions.Item
                                        label="出勤薪资"
                                        span={3}
                                    >
                                        {attendSalary}
                                    </Descriptions.Item>
                                    <Descriptions.Item
                                        label="五险一金缴纳"
                                        span={3}
                                    >
                                        <Table
                                            dataSource={fiveInsurance}
                                            columns={tableColumns}
                                            pagination={false}
                                        ></Table>
                                    </Descriptions.Item>
                                    <Descriptions.Item
                                        label="税前薪资"
                                        span={3}
                                    >
                                        {fiveInsuranceSalary}
                                    </Descriptions.Item>
                                    <Descriptions.Item
                                        label="个税缴纳"
                                        span={3}
                                    >
                                        <Table
                                            dataSource={tax}
                                            columns={table2Columns}
                                            pagination={false}
                                        ></Table>
                                    </Descriptions.Item>
                                    <Descriptions.Item
                                        label="到手薪资"
                                        span={3}
                                    >
                                        <span
                                            style={{
                                                fontSize: "24px",
                                                fontWeight: "bold"
                                            }}
                                        >
                                            ￥{finalSalary}
                                        </span>
                                    </Descriptions.Item>
                                </Descriptions>
                            </Col>
                        </Row>
                    </Layout.Content>
                )}
            </Layout>
        );
    }
}

export default Form.create({ name: "wpg" })(Index);
